import { Component, Host, h, Prop, EventEmitter, Event } from '@stencil/core';
import type { TpColor } from '../../../utils/types/color.type';
import {
  TpPaginationButtonConfig,
  TpPaginationDirection,
  TpPaginationSize,
} from './utils/pagination.type';
import { TP_PAGINATION } from './utils/pagination.contant';
import { createColorClasses } from '../../../utils/functions/color.function';

@Component({
  tag: 'tp-pagination',
  styleUrl: 'pagination.scss',
  scoped: true,
})
export class TpPagination {
  /**
   * A variação de cor dos botões do componente.
   */
  @Prop({ reflect: true }) color: TpColor = TP_PAGINATION.DEFAULT_COLOR;

  /**
   * A variação de tamanho dos botões do componente.
   */
  @Prop({ reflect: true }) size: TpPaginationSize = TP_PAGINATION.DEFAULT_SIZE;

  /**
   * O número total de páginas.
   */
  @Prop({ reflect: true }) pageCount: number;

  /**
   * Índice da página atual.
   */
  @Prop({ reflect: true }) currentPage: number;

  /**
   * Emitido quando há clique no botão de uma página específica.
   */
  @Event() tpNavigate: EventEmitter<number>;

  /**
   * Emitido quando há clique no botão de seguir para a página anterior.
   */
  @Event() tpPrevious: EventEmitter<null>;

  /**
   * Emitido quando há clique no botão de seguir para a próxima página.
   */
  @Event() tpNext: EventEmitter<null>;

  /**
   * Emitido quando há clique no botão que oculta páginas
   * específicas, não mostradas nos botões do estado atual.
   */
  @Event() tpPageJump: EventEmitter<TpPaginationDirection>;

  onPaginationButtonClick(buttonConfig: TpPaginationButtonConfig) {
    const { label, role, direction } = buttonConfig;

    if (role === TP_PAGINATION.ROLE.GOTO) return this.tpNavigate.emit(+label);

    if (role === TP_PAGINATION.ROLE.HIDDEN)
      return this.tpPageJump.emit(direction);

    if (role === TP_PAGINATION.ROLE.PREVIOUS) return this.tpPrevious.emit();

    if (role === TP_PAGINATION.ROLE.NEXT) return this.tpNext.emit();
  }

  getPaginationButtons() {
    const { color, size, pageCount, currentPage } = this;

    return Array.from({ length: pageCount })
      .map((_, index) => {
        const pageIndex = index + 1;

        /**
         * Se o número total de páginas (pageCount) for menor que 'BUTTON_LIMIT',
         * mostra todos os botões de navegação
         * BUTTON_LIMIT: numéro máximo que o total de páginas (pageCount)
         * deve ter para que sejam mostrados todos os botões de navegação.
         */
        if (pageCount <= TP_PAGINATION.BUTTON_LIMIT) {
          return {
            label: pageIndex,
            role: TP_PAGINATION.ROLE.GOTO,
            direction: null,
          } as TpPaginationButtonConfig;
        }

        /**
         * Se a página atual for a primeira ou a última e a distância
         * entre os índices do botão atual e página atual for menor ou igual a 2,
         * o botão do índice atual deverá ser renderizado.
         */
        if (
          [1, pageCount].includes(currentPage) &&
          Math.abs(pageIndex - currentPage) <= 2
        ) {
          return {
            label: pageIndex,
            role: TP_PAGINATION.ROLE.GOTO,
            direction: null,
          } as TpPaginationButtonConfig;
        }

        /**
         * Se o índice atual estiver entre o primeiro e a página atual (currentPage),
         * o primeiro botão a cair nessa condição será renderizado como hidden
         * e representará todos os outros índices que cairem nessa condição em
         * sequência, até encontrar um botão que será renderizado.
         */
        if (pageIndex > 1 && pageIndex < currentPage - 1) {
          return {
            label: TP_PAGINATION.HIDDEN_BUTTON_LABEL,
            role: pageIndex - 1 === 1 ? TP_PAGINATION.ROLE.HIDDEN : null,
            direction: TP_PAGINATION.DIRECTION.BACKWARD,
          } as TpPaginationButtonConfig;
        }

        /**
         * Se o índice atual estiver entre a página atual (currentPage) e a última
         * página, o primeiro botão a cair nessa condição será renderizado como
         * hidden e representará todos os outros índices que cairem nessa condição em
         * sequência, até encontrar um botão que será renderizado.
         */
        if (pageIndex < pageCount && pageIndex > currentPage + 1) {
          return {
            label: TP_PAGINATION.HIDDEN_BUTTON_LABEL,
            role:
              pageIndex === pageCount - 1 ? TP_PAGINATION.ROLE.HIDDEN : null,
            direction: TP_PAGINATION.DIRECTION.FORWARD,
          } as TpPaginationButtonConfig;
        }

        /**
         * Caso 'default' para todos os botões com índices que não caem
         * em nenhuma das condições acima.
         */
        return {
          label: pageIndex,
          role: TP_PAGINATION.ROLE.GOTO,
          direction: null,
        } as TpPaginationButtonConfig;
      })
      .filter(({ role }) => role)
      .map((buttonConfig) => (
        <ion-button
          onClick={() => this.onPaginationButtonClick(buttonConfig)}
          class="tp-pagination__item"
          color={color}
          // @ts-ignore
          size={size}
          disabled={+buttonConfig.label === currentPage}
        >
          {buttonConfig.label}
        </ion-button>
      ));
  }

  render() {
    const { color, size, pageCount, currentPage } = this;

    const paginationButtons = this.getPaginationButtons();

    return (
      <Host class={createColorClasses(color, { ['tp-pagination']: true })}>
        {pageCount > 1 && (
          <ion-button
            onClick={() =>
              this.onPaginationButtonClick({
                role: TP_PAGINATION.ROLE.PREVIOUS,
              })
            }
            class="tp-pagination__item"
            color={color}
            // @ts-ignore
            size={size}
            fill="clear"
            disabled={currentPage === 1}
          >
            <ion-icon
              slot="start"
              src="./assets/tp-chevron-left.svg"
            ></ion-icon>
          </ion-button>
        )}

        {paginationButtons}

        {pageCount > 1 && (
          <ion-button
            onClick={() =>
              this.onPaginationButtonClick({ role: TP_PAGINATION.ROLE.NEXT })
            }
            class="tp-pagination__item"
            color={color}
            // @ts-ignore
            size={size}
            fill="clear"
            disabled={currentPage === pageCount}
          >
            <ion-icon
              slot="start"
              src="./assets/tp-chevron-right.svg"
            ></ion-icon>
          </ion-button>
        )}
      </Host>
    );
  }
}
